import React from "react";

import "./ToolCard.css";

function ToolCard({ render_data }) {
  return (
    <div className="tool-card">
      <h3 className="head-text-1">{render_data.title}</h3>
      <hr className="global-hr-tag" />
      <p className="sub-text-1 sub-text-margin-bottom">
        {render_data.subTitle}
      </p>
      <a className="a-tag-style" href={render_data.url}>
        {render_data.urlTxt}
      </a>
    </div>
  );
}

export default ToolCard;
