import React from "react";

import NavBar from "../../../../components/NavBar/NavBar";
import tool_list from "../../../../Data/ToolList";
import ToolCard from "../ToolCard/ToolCard";
import StartQuoteBtn from "../../../../components/StartQuoteBtn/StartQuoteBtn";

import "./ToolsHome.css";

function ToolsHome() {
  return (
    <div className="tools-home">
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7 center">
            <img
              className="h-img"
              src="/images/tools/t-1.png"
              alt="tools"
            ></img>
            <h3 className="header-text-2">FHA Loan Tools and Resources</h3>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5">
          <div className="col-sm-12">
            <div className="row">
              <div className="col-sm-6">
                <ToolCard render_data={tool_list[0]} />
              </div>
              <div className="col-sm-6">
                <ToolCard render_data={tool_list[1]} />
              </div>
            </div>
          </div>
        </div>
        <div className="row d-flex justify-content-center mb-5">
          <div className="col-sm-12">
            <div className="row">
              <div className="col-sm-6">
                <ToolCard render_data={tool_list[2]} />
              </div>
              <div className="col-sm-6">
                <ToolCard render_data={tool_list[3]} />
              </div>
            </div>
          </div>
        </div>
      </div>
      <StartQuoteBtn />
    </div>
  );
}

export default ToolsHome;
